<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
      </slot>
    </div>
    <div class="dots">
      <span class="dot" v-for="(dot,index) in dots" :class="{active:currentPageIndex===index}"></span>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import { addClass } from '@/utils/dom'
export default {
  name: 'slider',
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 4000
    }
  },
  data() {
    return {
      dots: [],
      children: [],
      currentPageIndex: 0,
      timer: ''
    }
  },
  mounted() {
    setTimeout(() => {
      this._setSliderWidth()
      this._initDot()
      this._initSlider()
      if (this.autoPlay) {
        this._play()
      }
    }, 20)
    window.addEventListener('resize', () => {
      if (!this.slider) {
        return
      }
      this._setSliderWidth(true)
      this.slider.refresh()
    })
  },
  beforeMount() {
  },
  destroyed() {
    this.slider.disable()
    clearTimeout(this.timer)
  },
  activated() {
    this.slider.enable()
    let pageIndex = this.slider.getCurrentPage().pageX;
    console.log(pageIndex)
    console.log(this.dots.length)
    if (pageIndex > this.dots.length) {
      pageIndex = pageIndex % this.dots.length
    }
    this.slider.goToPage(pageIndex, 0, 0)
    if (this.loop) {
      pageIndex -= 1
    }
    this.currentPageIndex = pageIndex
    if (this.autoPlay) {
      this._play()
    }
  },
  methods: {
    _setSliderWidth(isResize) {
      this.chidren = this.$refs.sliderGroup.children;
      let width = 0;
      let sliderWidth = this.$refs.slider.clientWidth;
      for (let i = 0; i < this.chidren.length; i++) {
        let chid = this.chidren[i];
        addClass(chid, 'slider-item');
        chid.style.width = sliderWidth + 'px';
        width += sliderWidth
      }
      if (this.loop && !isResize) {
        width += 2 * sliderWidth
      }
      this.$refs.sliderGroup.style.width = width + 'px'

    },
    _initSlider() {
      this.slider = new BScroll(this.$refs.slider, {
        scrollX: true,
        scrollY: false,
        momentum: false,
        snap: {
          loop: this.loop,
          threshold: 0.3,
          speed: 400
        },
        click: true
      })
      this.slider.on('scrollEnd', () => {
        let pageIndex = this.slider.getCurrentPage().pageX;
        if (this.loop) {
          pageIndex -= 1
        }
        this.currentPageIndex = pageIndex;
        if (this.autoPlay) {
          clearTimeout(this.timer)
          this._play()
        }
      })
    },
    _initDot() {
      console.log(this.chidren.length)
      this.dots = new Array(this.chidren.length)
      console.log(this.dots)
    },
    _play() {
      let pageIndex = this.slider.getCurrentPage().pageX + 1
      this.timer = setTimeout(() => {
        this.slider.goToPage(pageIndex, 0, 400)
      }, this.interval)

    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        transition: width 0.3s;
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>


